<template>
  <el-container>
    <el-main class="text-center">
      <el-card class="login-box display-inline-block">
        <el-row> <span class="login-title">登录</span> </el-row>
        <el-form :model="loginForm" label-width="auto" :rules="rules" class="login-form">
          <el-form-item label="账户" prop="username">
            <el-input v-model="loginForm.username" placeholder="请输入账户"></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="loginForm.password" type="password" placeholder="请输入密码"></el-input>
          </el-form-item>
        </el-form>
        <el-row>
          <el-button type="primary" icon="el-icon-check" size="small" :loading="loginLoading" @click="loginSubmit">登录</el-button>
          <el-link type="info" class="login-forget-link"><el-icon class="el-icon-question"></el-icon>忘记密码</el-link>
        </el-row>
      </el-card>
    </el-main>
  </el-container>
</template>

<script>
    export default {
      name: "login",
      data() {
        return {
          loginForm:{
            username:'',
            password:''
          },
          rules:{
            username:[{required:true,message:'请输入账户',trigger:'blur'}],
            password:[{required:true,message:'请输入密码',trigger:'blur'}]
          },
          loginLoading:false
        }
      },
      methods:{
        loginSubmit(){
          this.loginLoading=true
          this.$ajax({
            url:'/login'
          }).then(res=>{
            console.log(res)
            this.loginLoading=false
          })
        }
      }
    }
</script>

<style scoped>
  .login-box{
    width: 400px;
  }
  .login-title{
    font-size: 18px;
    line-height: 32px;
    height: 32px;
    display: inline-block;
  }
  .login-form{
    margin-top: 15px;
    padding: 0 20px;
  }
  .login-forget-link{
    position: absolute;
    top: 0;
    bottom: 0;
    line-height: 24px;
    height: 24px;
    margin: auto;
    right: 0;
    font-size: 12px;
  }
</style>



